body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.site-header {
  height: 12vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(to bottom, #245199 , #2c61b8);
}

.site-header p {
  font-size: 48px;
  font-weight: bolder;
  color: #ffffff;
  margin: 0;
}

.site-header img {
  width: 48px;
  margin: 0 16px;
}

.body-content {
  height: 100%;
  background-color: #abbcd8;
}
.body {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 100%;
  justify-content: center;
  align-items: center;
  padding-top: 20px;
}

.spinner-wrapper {
  height: 86vh;
}

.reload-btn {
  position: absolute;
  top: 24px;
  right: 12px;
  background-color: inherit;
  border: none;
  margin: 0;
  padding: 0;
  cursor: pointer;
}

.reload-btn:focus {
  outline: 0;
}

.reload {
  height: 32px;
  width: auto;
}

.reload:hover {
  opacity: 0.4;
}
.clinic-item {
  display: flex;
  flex-direction: column;
  width: 24vw;
  height: 360px;
  margin: 30px;
  padding: 20px;
  border-radius: 8px 32px;
  background-color: rgba(255, 255, 255, 0.4);
  box-shadow: 1px 2px 8px 1px rgba(0, 0, 0, 0.3);
  overflow: hidden;
}

.clinic-item:hover {
  background-color: rgb(255, 255, 255, 0.8);
}

.patient-list {
  overflow-y: auto;
}

.patient-btn {
  display: block;
  font-size: 18px;
  font-weight: 500;
  background-color: inherit;
  border: none;
  margin: 16px 0;
  padding: 0;
  cursor: pointer;
}

.patient-btn:focus {
  outline: 0;
}

.patient-btn p {
  margin: 0px;
}

.patient-btn p:hover {
  color: #2c61b8;
}

.clinic-name {
  font-size: 24px;
  font-weight: bolder;
  padding: 0;
  margin: 0;
}

.pop-btn {
  font-size: 16px;
  color: #fff;
  border: none;
  border-radius: 4px;
  background-color: #245199;
  cursor: pointer;
  padding: 4px 8px;
  margin: 4px 0;
}

.pop-btn:hover {
  background-color: #2c61b8;
}

.clinic-address {
  font-size: 18px;
  font-style: italic;
  padding: 0;
  margin: 8px 0;
}

.waittime {
  font-size: 18px;
  font-weight: bolder;
  padding: 0;
  margin: 6px 0;
}

/* .modal-content {
  display: flex;
  justify
} */

.modal-title {
  font-size: 36px;
  font-weight: bolder;
  margin: 0 0 16px;
}

.patient-info {
  font-size: 20px;
}

.close-modal {
  position: absolute;
  background-color: inherit;
  border: none;
  cursor: pointer;
  padding: 0;
  margin: 8px;
  right: 0;
  top: 0;
}

.close-modal img {
  height: 18px;
  width: 18px;
}

/* CUSTOM SCROLLBAR */

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2px grey; 
  border-radius: 4px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #638ac9; 
  border-radius: 4px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #2f65bb; 
}
#spinner {
  display: flex;
  align-items: center;
  height: 70%;
}

#spinner-icon {
  -webkit-animation: spin infinite 2s linear;
          animation: spin infinite 2s linear;
  height: 65px;
  width: 65px;
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
